html {
  height: 100%;
  scroll-behavior: smooth;
}

body {
  margin: 0;
  padding: 0;
  min-height: 100%;
  display: grid;
  grid-template-rows: auto 1fr auto;

  font-family: 'Roboto', sans-serif;
  font-size: 12px;
  line-height: 1.333;

    ::-webkit-scrollbar {
        width: 5px;
        height: 10px;
        background-color: $clr-light;
      }
    
      ::-webkit-scrollbar-thumb {
        background-color: rgba(#FF6B01, 0.3);
        border-radius: 10px;
        transition: background-color $transition-time $cubic;
    
        &:hover {
          background-color: rgba(#FF6B01, 0.7);
        }
      }
}
